import 'ol/ol.css';
import Map from 'ol/Map';
import OSM from 'ol/source/OSM';

import ImageWMS from 'ol/source/ImageWMS';
import {Image as ImageLayer, Tile as TileLayer} from 'ol/layer';

import View from 'ol/View';

var orka = new ImageLayer({
  // extent: [-13884991, 2870341, -7455066, 6338219],
  source: new ImageWMS({
    url: 'https://www.orka-mv.de/geodienste/orkamv/wms',
    params: {'LAYERS': 'orkamv'},
    ratio: 1
  })
});
var bielefeld = new ImageLayer({
  source: new ImageWMS({
    url: 'http://www.bielefeld01.de/md/WMS/bielefeld_karte_farbe/02',
    params: {'LAYERS': 'map'},
    ratio: 1
  })
});

var map1 = new Map({
  layers: [orka],
  target: 'map1',
  view: new View({
    projection: 'EPSG:3857',
    center: [1350489.415742, 7186456.525316],
    zoom: 16
  }),
});
var map2 = new Map({
  layers: [bielefeld],
  target: 'map2',
  view: new View({
    projection: 'EPSG:3857',
    center: [949710.967186, 6803855.757549],
    zoom: 16
  }),
});
